<template>
    <section>
        <h2>API list</h2>
        <el-tree 
            :data="data"
            :filter-node-method="setSearch"
            highlight-current
            @node-click="eClick"
        ></el-tree>
    </section>
</template>
<style>
    .el-tree {
        background: none;
        border: none;
        color: #324057
    }
    
    .el-tree .el-tree-node__expand-icon {
        border-left-color: #324057;
    }
    
    .doc-sd h2 {
        padding: 25px;
        text-align: center;
        color: #EFF2F7;
        background: #475669
    }
    
    .doc-sd>section {
        height: 100%;
        overflow-y: scroll;
    }
</style>
<script>
    import CTRL from '../CTRL.js';

    export default {
        data() {
            return {
                data: [{
                    label: '一级 1',
                    children: [{
                        label: '二级 1-1'
                    }]
                }, {
                    label: '一级 2',
                    children: [{
                        label: '二级 2-1'
                    }, {
                        label: '二级 2-2'
                    }]
                }, {
                    label: '一级 3',
                    children: [{
                        label: '二级 3-1'
                    }, {
                        label: '二级 3-2'
                    }]
                }],
                search: ''
            }
        },
        computed: {

        },
        methods: {
            eClick(data) {
                if (data.info) {
                    CTRL.$emit('nodeInfo', data.info);
                }
            },
            setSearch(value, data) {
                if (!this.search) return true;
                return data.label.indexOf(this.search) !== -1;
            }
        },
        mounted() {
            this.$http.get('/doc/auto-route-view?type=tree').then(res => {
                this.data = res.body
            });
        }
    }
</script>


