export default {
    name: '获取express-dir-route读到的接口列表',
    summary: 'to get list of data which read by express-dir-route ',
    method: 'GET',
    route: '/doc/auto-route-view',
    content: [{
        title: 'query',
        table: {
            thead: ['Attribute 参数', 'type 类型', 'information 说明信息'],
            tbody: [
                ['type', 'String', '选择输出数据形式。当type=nomal时，输出一个正常数组。当type=tree时，输出一个树形数组。当type为其他值或空的时候，输出本页面'],
            ]
        },
    }, {
        title: '说明',
        summary: 'express-dir-route会根据你在编写接口文件时写的信息去生成一个这样的接口文档。访问\'/doc/auto-route-view\'这一个路径时进入本页面'
    }],
    test: {
        query: {
            'type': 'nomal',
        },
    }
}


