<template>
	<div id="app">
		<header class="doc-hd">
			<h1>express-dir-route.doc</h1>
		</header>
		<section class="doc-ct">
			<aside class="doc-sd" :class="asideClass">
				<div class="doc-btn_hide-sd" @click="eHideSide">
					<i class="el-icon-arrow-left" :class="asideClass"></i>
				</div>
				<doc-sd></doc-sd>
			</aside>
			<div class="doc-mn" :class="asideClass">
				<doc-main></doc-main>
			</div>
		</section>  
		<footer class="doc-ft">
			<p>express-dir-route</p>
		</footer>
	</div>
</template>

<script>
    import rightMain from './component/rightMain.vue';
    import leftSide from './component/leftSide.vue';
    export default {
        name: 'app',
        data() {
            return {
                nLeftSideSpan: 0,
                isShowSide: false
            }
        },
        computed: {
            asideClass() {
                return {
                    'doc-sd-hide': this.isShowSide
                }
            }
        },
        components: {
            'doc-main': rightMain,
            'doc-sd': leftSide
        },
        methods: {
            eHideSide() {
                this.isShowSide = !this.isShowSide
            },
        }
    }
</script>

<style>
    body,
    html {
        height: 100%;
        margin: 0;
        padding: 0;
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
    }
    
    #app {
        position: relative;
        height: 100%;
    }
    
    h1,
    h2,
    h3,
    p {
        margin: 0;
        font-weight: initial;
    }
    
    .doc-hd {
        height: 50px;
        line-height: 50px;
        padding: 0 15px;
        background: #324057;
        color: #fff;
    }
    
    .doc-ct {
        position: absolute;
        top: 50px;
        bottom: 25px;
        width: 100%;
        overflow: hidden;
    }
    
    .doc-sd {
        position: absolute;
        left: -300px;
        float: left;
        width: 300px;
        height: 100%;
        background: #99A9BF;
        transform: translateX(100%);
        transition: all .5s;
    }
    
    .doc-mn {
        height: 100%;
        padding-left: 25px;
        overflow-y: auto;
        overflow-x: hidden;
        margin-left: 300px;
        transition: all .5s;
    }
    
    .doc-ft {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 25px;
        line-height: 25px;
        text-align: center;
        color: #a4aebd;
        background: #324057;
    }
    
    .doc-btn_hide-sd {
        position: absolute;
        top: 50%;
        right: -25px;
        height: 50px;
        line-height: 50px;
        width: 25px;
        border-radius: 0 15px 15px 0;
        margin-top: -25px;
        text-align: center;
        background: #324057;
        color: #fff;
        transition: all .5s;
        cursor: pointer;
        opacity: 0.5
    }
    
    .doc-btn_hide-sd:hover {
        background: #475669;
        opacity: 1
    }
    
    .doc-btn_hide-sd i {
        transition: all .5s;
    }
    
    .doc-sd.doc-sd-hide {
        transform: translateX(0)
    }
    
    .doc-mn.doc-sd-hide {
        margin: 0
    }
    
    .doc-btn_hide-sd i.doc-sd-hide {
        transform: rotate(180deg);
    }
</style>


