<template>
    <section class="doc-test">
        <h3>TEST</h3>
        <p>{{test.text}}</p>
        <dl v-if="test.query">
            <dt>query</dt>
            <dd v-for="item,attr,index in test.query">
                <el-input v-model="item" @change="setValue(test.query,attr,item)">
                    <span slot="prepend">{{attr}}</span>
                </el-input>
            </dd>
        </dl>
        <dl v-if="test.data">
            <dt>data</dt>
            <dd v-for="item,attr,index in test.data">
                <el-input v-model="item" @change="setValue(test.data,attr,item)">
                    <span slot="prepend">{{attr}}</span>
                </el-input>
            </dd>
        </dl>
        <table>
            <tr>
                <th>url</th>
                <td>{{url}}</td>
                <td rowspan="2">
                    <el-button :plain="true" @click="eSendTest">send</el-button>
                </td>
            </tr>
            <tr>
                <th>method</th>
                <td>
                    <b>{{info.method}}</b>
                </td>
            </tr>
        </table>
    </section>
</template>
<style>
    .doc-test {
        padding: 5px;
        border: 1px solid #58B7FF;
        margin-top: 25px;
        background: #F9FAFC
    }
    
    .doc-test dt {
        font-weight: 700;
        color: #324057
    }
    
    .doc-test dt,
    .doc-test dd {
        margin-top: 5px;
    }
    
    .doc-test table {
        width: 100%;
        border: 1px solid #D3DCE6;
        background: #EFF2F7;
    }
    
    .doc-test th {
        text-align: right;
        color: #324057
    }
    
    .doc-test td,
    .doc-test th {
        padding: 5px;
    }
</style>
<script>
    import CTRL from '../CTRL.js';
    import Message from 'element-ui/lib/message';

    export default {
        props: ['info'],
        computed: {
            url() {
                if (this.test.query) {
                    var a = [];
                    for (let attr in this.test.query) {
                        if (this.test.query[attr]) a.push(attr + '=' + this.test.query[attr]);
                    }
                    if (a.length === 0) {
                        return this.info.route
                    }
                    return this.info.route + '?' + a.join('&')
                }
                return this.info.route
            },
            test() {
                if (this.info.test) {
                    return this.info.test
                }
                return
            },
            bodyData() {
                if (this.test.data) {
                    var json = {};
                    for (let attr in this.test.data) {
                        if (this.test.data[attr]) {
                            json[attr] = this.test.data[attr]
                        }
                    }
                    return json;
                }
                return {}
            }
        },
        methods: {
            setValue(obj, attr, value) {
                this.$set(obj, attr, value);
            },
            eSendTest() {
                var m = this.info.method;
                var u = this.url;
                Message('请求已经发送');
                var msg = '信息已从服务器返回'
                this.$http({
                    method: m,
                    url: u,
                    body: this.bodyData,
                    header: {
                        'Content-Type': 'application/x-www-form-urlencoded;'
                    }
                }).then(res => {
                    CTRL.$emit('result', res.body);
                    Message.success(msg)
                }, error => {
                    CTRL.$emit('result', error.body);
                    Message.warning(msg)
                })
            }
        }
    }
</script>


