<template>
    <section class="doc-mn_box">
        <h2>{{ct.name}}</h2>
        <p class="doc-summary">{{ct.summary}}</p>
        <h3>Url:<strong>{{ct.route}}</strong></h3>
        <h3>Method:<strong>{{ct.method}}</strong></h3>
        <doc-sec v-for="item in ct.content" :item="item"></doc-sec>
        <doc-test v-if="ct.test" :test="ct.test" :info="ct"></doc-test>
        <doc-res></doc-res>
    </section>
</template>
<style>
    .doc-mn h2 {
        padding-bottom: 5px;
        border-bottom: 1px solid #1D8CE0;
        margin-bottom: 15px;
        color: #1F2D3D;
    }
    
    .doc-mn_box {
        padding: 25px 0;
        padding-right: 25px;
    }
    
    h3 strong {
        margin: 5px;
        font-size: 25px;
        color: #FF4949;
    }
</style>
<script>
    import CTRL from '../CTRL.js';

    import data from '../data/ct.js';

    import sec from './mainSection.vue';
    import test from './test.vue';
    import result from './result.vue';
    export default {
        data() {
            return {
                ct: data
            }
        },
        components: {
            'doc-sec': sec,
            'doc-test': test,
            'doc-res': result
        },
        mounted() {
            CTRL.$on('nodeInfo', info => {
                this.ct = info
            });
        }
    }
</script>


