<template>
    <section>
        <transition name="tr-md" >
            <div class="doc-md" v-if="res" v-html="trMd"></div>
        </transition>
    </section>
</template>
<style>
    .doc-md {
        padding: 5px;
        border: 1px solid #D3DCE6;
        margin-top: 15px;
    }
    
    .doc-md pre {
        width: 100%;
        overflow-x: auto;
    }
    
    .doc-md pre * {
        font-family: 'Courier New'
    }
    
    .doc-md .hljs-attr {
        font-weight: bold;
    }
    
    .tr-md-enter-active,
    .tr-md-leave-active {
        transition: all .5s;
        opacity: 1;
        transform: translate(0, 0);
    }
    
    .tr-md-enter,
    .tr-md-leave-active {
        opacity: 0;
        transform: translate(0, 50px);
    }
</style>
<script>
    import CTRL from '../CTRL';
    import marked from 'marked/lib/marked';
    import hl from 'highlight.js/lib/highlight.js';
    import hl_json from 'highlight.js/lib/languages/json'
    hl.registerLanguage('json', hl_json);
    marked.setOptions({
        renderer: new marked.Renderer(),
        gfm: true,
        tables: true,
        breaks: false,
        pedantic: false,
        sanitize: true,
        smartLists: true,
        smartypants: false,
        highlight(code) {
            return hl.highlightAuto(code).value
        }
    })

    export default {
        data() {
            return {
                res: ''
            }
        },
        computed: {
            trMd() {
                var code = '```javascript\r' + JSON.stringify(this.res, null, 2) + '\r```'
                return marked(code);
            }
        },
        mounted() {
            CTRL.$on('nodeInfo', () => {
                this.res = '';
            });

            CTRL.$on('result', res => {
                this.res = res;
            })
        }
    }
</script>


