<template>
    <section>
        <h3 v-if="item.title">{{item.title}}</h3>
        <p>{{item.summary}}</p>
        <el-table
            v-if="item.table"
            :data="item.table.tbody"
            border
            stripe
        >
            <el-table-column 
                :prop="'0'" 
                :label="item.table.thead[0]"
            ></el-table-column>
            <el-table-column
                v-for="ele,i in th"
                :prop="(i+1).toString()"
                :label="ele"
            ></el-table-column>
        </el-table>
        <div v-html="item.md"></div>
    </section>
</template>
<style>
    .doc-mn h3 {
        margin-top: 15px;
        padding-bottom: 5px;
        font-weight: bold;
        color: #324057
    }
    
    .el-table {
        margin: 5px;
    }
</style>
<script>
    export default {
        props: ['item'],
        computed: {
            th() {
                return this.item.table.thead.slice(1);
            }
        }
    }
</script>


